import React from "react";
import { useSelector } from "react-redux";
import { Table } from "react-bootstrap";
import { toSiPrefix } from "math";

const showUnit = (obj) => {
  if (obj.hasOwnProperty("unit")) {
    return obj.unit;
  }
};

const createTable = (columnsData, tableData) => {
  const tableHeaderElements = columnsData.map((el, id) => {
    return (
      <th
        className="report-table__cell"
        key={id}
        style={{ verticalAlign: "middle" }}
      >
        {el.title}
        <br />
        {showUnit(el)}
      </th>
    );
  });

  const tableBodyRows = tableData.map((el, id) => {
    const tableCells = Object.entries(el).map((item, itemId) => {
      return (
        <td className="report-table__cell" key={itemId}>
          {item[1]}
        </td>
      );
    });
    return (
      <tr style={{ textAlign: "center" }} key={id}>
        {tableCells}
      </tr>
    );
  });

  return { tableHeaderElements, tableBodyRows };
};

const ProjectTable = () => {
  const project = useSelector((state) => state.project);

  const columnsData = [
    { title: "Name", field: "name" },
    { title: "Outside temp", unit: "[]", field: "outsideT" },
    { title: "Ground temp", unit: "[]", field: "groundT" },
    { title: "Degree days", field: "dDays" },
    { title: "Total floor area", unit: "[m]", field: "totalFloorArea" },
    { title: "Specific heat loss", unit: "[W]", field: "totalHeatLoss" },
    { title: "Total-heat-loss", unit: "[W/m]", field: "specHeatLoss" },
  ];

  const tableData = [
    {
      name: project.name,
      outsideT:  project.outsideT,
      groundT:  project.groundT,
      dDays: Number(toSiPrefix(project.dDays,undefined, " ").value).toFixed(2),
      totalFloorArea:  Number(toSiPrefix(project.totalFloorArea,undefined, " ").value).toFixed(2),
      specHeatLoss:  Number(toSiPrefix(project.specHeatLoss,undefined, " ").value).toFixed(2),
      totalHeatLoss:  Number(toSiPrefix(project.totalHeatLoss,undefined, " ").value).toFixed(2),
    },
    
  ];

  const tableUi = createTable(columnsData, tableData);

  return (
    <Table striped bordered hover responsive="md">
      <thead>
        <tr style={{ textAlign: "center" }}>{tableUi.tableHeaderElements}</tr>
      </thead>
      <tbody>{tableUi.tableBodyRows}</tbody>
    </Table>
  );
};

export default ProjectTable;
