import { Form } from "react-bootstrap";

const Input = (props) => {
  const { placeholder, type, value, name, handlerChange } = props;
  return (
    <Form.Group>
      <Form.Label>{placeholder}</Form.Label>
      <Form.Control
        size="sm"
        type={type}
        placeholder={placeholder}
        value={value}
        name={name}
        onChange={handlerChange}
      />
    </Form.Group>
  );
};

const SelectInput = (props) => {
  const { placeholder, name, value, handlerChange, isDisabled, options } =
    props;

  return (
    <Form.Group>
      <Form.Label>{placeholder}</Form.Label>
      <Form.Control
        disabled={isDisabled}
        size="sm"
        as="select"
        name={name}
        value={value}
        onChange={handlerChange}
      >
        {options}
      </Form.Control>
    </Form.Group>
  );
};

export const NumberInput = (props) => {
  const { placeholder, handlerChange, value, name, step } = props;
  return (
    <Form.Group>
      <Form.Label>{placeholder}</Form.Label>
      <Form.Control
        size="sm"
        type="number"
        placeholder={placeholder}
        value={value}
        name={name}
        step={step}
        onChange={handlerChange}
      />
    </Form.Group>
  );
};

export const TextInput = (props) => {
  return <Input {...props} type="text" />;
};

