import React from "react";
import { Col } from "react-bootstrap";
import RoomBuildingItemTable from "../RoomBuildingItemTable/RoomBuildingItemTable";


const RoomBuildingItemTables = (props) => {
  const items = props.items.map((roomItem, id) => {
    return (
      <Col
        xl={4}
        xs={12}
        lg={6}
        md={12}
        sm={12}
        key={roomItem.id}
        className="mt-5"
      >
        <RoomBuildingItemTable
          editRoomBuilding={props.editRoomBuilding}
          deltaT={props.deltaT}
          itemIdx={id}
          roomIdx={props.roomIdx}
          room={props.room}
          item={roomItem}
          outsideT={props.outsideT}
          rooms={props.rooms}
          projectBuildingType={props.projectBuildingType}
        />
      </Col>
    );
  });
  return items;
};

export default RoomBuildingItemTables;
