import { calculate_project_data } from "calculation/calculate_project_data";
import { CALCULATE_PROJECT_DATA, EDIT_PROJECT_DATA } from "./actionTypes";

// initialState interface = {
//   name: NAME OF PROJECT - STRING,
//   outsideT: OUTSIDE TEMPERATURE, NUMBER,
//   groundT: GROUND TEMPERATURE, NUMBER,
//   dDays: DEGREE DAYS, NUMBER,
//   totalHeatLoss: TOTAL HEAT LOSS OF PROJECT, NUMBER,
//   venHeatLoss: VENTILATION HEAT LOSS OF PROJECT, NUMBER,
//   totalFloorArea: TOTAL FLOOR AREA OF PROJECT, NUMBER,
//   transmissionLoss: TRANSMISSION HEAT LOSS OF PROJECT, NUMBER,
//   yearlyHeat: YEARLY HEAT LOSS OF PROJECT, NUMBER,
//   buildingType: THERMAL BRIDGING VALUE, NUMBER,
// };

const initialState = {
  name: "Project 1 - Home",
  outsideT: -3,
  groundT: 20,
  dDays: 2000,
  totalHeatLoss: 0,
  venHeatLoss: 0,
  totalFloorArea: 0,
  transmissionLoss: 0,
  yearlyHeat: 0,
  buildingType: 0,
  ceilingHeatLoss: 0,
};

export const projectReducer = (state = initialState, action) => {
  switch (action.type) {
    case CALCULATE_PROJECT_DATA:
      return calculate_project_data({ ...state }, action.payload.data);
    case EDIT_PROJECT_DATA:
      return { ...state, ...action.payload.data };
    default:
      return state;
  }
};
