import { useState } from "react";
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap";
import { NavLink } from "react-router-dom";

const AppHeader = (props) => {
  const { sidebarBtn } = props;

  const [activeLink, setActiveLink] = useState();

  return (
    <Navbar collapseOnSelect expand="md" variant="dark" className="app-header">
      <Container fluid>
        {sidebarBtn}
        <Navbar.Brand>
          <NavLink
            to="/main/start"
            activeClassName="app-header__nav-item_active"
            className="nav-item app-header__nav-item"
          >
            APPLICATION
          </NavLink>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto app-header__nav nav ms-4">
            <NavLink
              to="/main/mat-app"
              activeClassName="app-header__nav-item_active"
              className="nav-item app-header__nav-item me-3"
            >
              Mat-app
            </NavLink>
            <NavLink
              to="/main/nomograph"
              activeClassName="app-header__nav-item_active"
              className="nav-item app-header__nav-item me-3"
            >
              Nomographs
            </NavLink>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">More deets</Nav.Link>
            <Nav.Link eventKey={2} href="#memes">
              Dank memes
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default AppHeader;
