/* eslint-disable react-hooks/exhaustive-deps */
import ItemTable from "../../../common/heatLossCalcCommon/ItemTable/ItemTable";
import { useDispatch } from "react-redux";
import { getTableData } from "./functions/getTableData";
import {
  calculate_item_data,
  calculate_wallItem_data,
  delete_wallItem_action,
} from "../../../../redux/store/rooms/actions";
import { useEffect } from "react";

const RoomBuildingOpeningItemTable = (props) => {
  const {
    editRoomBuildingOpening,
    curRoomIdx,
    curRoomBuildingIdx,
    openingIdx,
    openingItem,
    buildingTypeValue,
    deltaT,
  } = props;
  const dispatch = useDispatch();

  const calculateData = {
    deltaT,
    buildingTypeValue,
  };

  const deleteOpeningItemTable = () => {
    if (window.confirm(`Do you want to delete ${openingItem.name}?`)) {
      const item = document.getElementById(openingItem.id);
      item.classList.add("flip-out-diag-2-br");
      setTimeout(() => {
        dispatch(
          delete_wallItem_action(curRoomIdx, curRoomBuildingIdx, openingIdx)
        );
        dispatch(
          calculate_item_data(curRoomIdx, curRoomBuildingIdx, calculateData)
        );
        item.classList.remove("flip-out-diag-2-br");
      }, 600);
    }
  };

  const editOpeningItemTable = () => {
    editRoomBuildingOpening({ ...openingItem, idx: openingIdx });
  };

  const tableData = getTableData(openingItem);

  const calculateOpening = () => {
    dispatch(
      calculate_wallItem_data(
        curRoomIdx,
        curRoomBuildingIdx,
        openingIdx,
        calculateData
      )
    );
    dispatch(
      calculate_item_data(curRoomIdx, curRoomBuildingIdx, calculateData)
    );
  };

  useEffect(() => {
    calculateOpening();
  }, []);

  useEffect(() => {
    calculateOpening();
  }, [
    openingItem.name,
    openingItem["length"],
    openingItem.height,
    openingItem.uValue,
    openingItem.area,
    openingItem.heatLoss,
    openingItem.itemName,
  ]);

  return (
    <ItemTable
      tableId={openingItem.id}
      tableName={openingItem.name}
      goToInfoTable={null}
      deleteTable={deleteOpeningItemTable}
      hasInfoTable={false}
      editTable={editOpeningItemTable}
      tableData={tableData}
    />
  );
};
export default RoomBuildingOpeningItemTable;
