import { CALCULATE_PROJECT_DATA, EDIT_PROJECT_DATA } from "./actionTypes";

export const calculate_project_data = (rooms) => {
  return {
    type: CALCULATE_PROJECT_DATA,
    payload: {
      data: rooms,
    },
  };
};

export const edit_project_action = (data) => {
  return {
    type: EDIT_PROJECT_DATA,
    payload: {
      data: data,
    },
  };
};
