import { validate_numbers } from "math/validate_numbers";
import { calc_group } from "./calc_group";

// prevState - previos project state
// data - new project data from form

export const calculate_project_data = (prevState, data) => {
  const totalHeatLoss = calc_group(data, "totalHeatLoss");
  const totalFloorArea = calc_group(data, "area");
  const specHeatLoss = totalHeatLoss / totalFloorArea || 0;

  const result = {
    totalHeatLoss: totalHeatLoss,
    venHeatLoss: calc_group(data, "venHeatLoss"),
    transmissionLoss: calc_group(data, "transmissionLoss"),
    ceilingHeatLoss: calc_group(data, "ceilingHeatLoss"),
    totalFloorArea: calc_group(data, "area"),
    yearlyHeat: calc_group(data, "yearlyHeat"),
    specHeatLoss: specHeatLoss,
  };

  validate_numbers(
    result.totalHeatLoss,
    "TOTAL HEAT LOSS",
    "PROJECT CALCULATION OBJECT"
  );
  validate_numbers(
    result.venHeatLoss,
    "VENTILATION HEAT LOSS",
    "PROJECT CALCULATION OBJECT"
  );
  validate_numbers(
    result.transmissionLoss,
    "TRANSMISSION HEAT LOSS",
    "PROJECT CALCULATION OBJECT"
  );
  validate_numbers(
    result.totalFloorArea,
    "TOTAL FLOOR AREA",
    "PROJECT CALCULATION OBJECT"
  );
  validate_numbers(
    result.yearlyHeat,
    "YEARLY HEAT LOSS",
    "PROJECT CALCULATION OBJECT"
  );
  validate_numbers(
    result.specHeatLoss,
    "SPECIAL HEAT LOSS",
    "PROJECT CALCULATION OBJECT"
  );

  return { ...prevState, ...result };
};
