import React, { useState } from "react";
import { Form } from "react-bootstrap";
import { Row, Button, Col } from "react-bootstrap";
import { useFormik } from "formik";
import { stat } from "../../nomographExample/settings";
import { MassFlowNomographUpdated } from "../../nomographExample/MassFlowNomographUpdated/MassFlowNomographUpdated";
import { value2pos } from "../coreFunctions";
import { minMax2parameter } from "../coreFunctions.js";

export const MassFlowNomographContent = (props) => {
  let cb = minMax2parameter(
    stat.userSetup.massFlowCalculation.scalePower,
    stat.userSetup.massFlowCalculation.scalePower.min,
    stat.userSetup.massFlowCalculation.scalePower.max
  );

  stat.userSetup.massFlowCalculation.scalePower.m = cb.m;
  stat.userSetup.massFlowCalculation.scalePower.n = cb.n;
  stat.userSetup.massFlowCalculation.scalePower.p = cb.p;
  stat.userSetup.massFlowCalculation.scalePower.q = cb.q;

  cb = minMax2parameter(
    stat.userSetup.massFlowCalculation.scaleTemperature,
    stat.userSetup.massFlowCalculation.scaleTemperature.min,
    stat.userSetup.massFlowCalculation.scaleTemperature.max
  );

  stat.userSetup.massFlowCalculation.scaleTemperature.m = cb.m;
  stat.userSetup.massFlowCalculation.scaleTemperature.n = cb.n;
  stat.userSetup.massFlowCalculation.scaleTemperature.p = cb.p;
  stat.userSetup.massFlowCalculation.scaleTemperature.q = cb.q;

  // initial form values
  const initialFormValues = {
    deltaTemp: stat.userInput.massFlowCalculation.deltaTemp,
    power: stat.userInput.massFlowCalculation.power / 1000,
    cb: 0,
    rho: 0,
  };

  const [massFlowCalculationS, setMassFlowCalculationS] = useState(
    stat.userInput.massFlowCalculation
  );

  const [deltaTDrugBtnState, setDeltaTDrugBtnState] = useState({
    isDragging: false,
    x: value2pos(
      stat.userSetup.massFlowCalculation.scaleTemperature,
      stat.userInput.massFlowCalculation.deltaTemp
    ).x,
    y: value2pos(
      stat.userSetup.massFlowCalculation.scaleTemperature,
      stat.userInput.massFlowCalculation.deltaTemp
    ).y,
  });

  const [powerDrugBtnState, setPowerDrugBtnState] = useState({
    isDragging: false,
    x: value2pos(
      stat.userSetup.massFlowCalculation.scalePower,
      stat.userInput.massFlowCalculation.power / 1000
    ).x,
    y: value2pos(
      stat.userSetup.massFlowCalculation.scalePower,
      stat.userInput.massFlowCalculation.power / 1000
    ).y,
  });

  const { handleSubmit, setFieldValue, values } = useFormik({
    initialValues: initialFormValues,
    onSubmit: (values) => {
      setMassFlowCalculationS(() => {
        return {
          ...massFlowCalculationS,
          deltaTemp: Number(values.deltaTemp),
          power: Number(values.power * 1000),
        };
      });
      setDeltaTDrugBtnState(() => {
        return {
          isDragging: false,
          x: deltaTDrugBtnState.x,
          y: value2pos(
            stat.userSetup.massFlowCalculation.scaleTemperature,
            Number(values.deltaTemp)
          ).y,
        };
      });
      setPowerDrugBtnState(() => {
        return {
          isDragging: false,
          x: powerDrugBtnState.x,
          y: value2pos(
            stat.userSetup.massFlowCalculation.scalePower,
            Number(values.power)
          ).y,
        };
      });
    },
  });

  const customHandleChange = (e) => {
    if (e.target.name === "deltaTemp") {
      if (
        e.target.value < stat.userSetup.massFlowCalculation.scaleTemperature.min
      ) {
        e.target.value =
          stat.userSetup.massFlowCalculation.scaleTemperature.min;
      } else if (
        e.target.value > stat.userSetup.massFlowCalculation.scaleTemperature.max
      ) {
        e.target.value =
          stat.userSetup.massFlowCalculation.scaleTemperature.max;
      }

      setFieldValue(e.target.name, e.target.value);

      setMassFlowCalculationS(() => {
        return {
          ...massFlowCalculationS,
          deltaTemp: Number(e.target.value),
        };
      });

      setDeltaTDrugBtnState(() => {
        return {
          isDragging: false,
          x: deltaTDrugBtnState.x,
          y: value2pos(
            stat.userSetup.massFlowCalculation.scaleTemperature,
            Number(e.target.value)
          ).y,
        };
      });
    }
    if (e.target.name === "power") {
      if (e.target.value < stat.userSetup.massFlowCalculation.scalePower.min) {
        e.target.value = stat.userSetup.massFlowCalculation.scalePower.min;
      } else if (
        e.target.value > stat.userSetup.massFlowCalculation.scalePower.max
      ) {
        e.target.value = stat.userSetup.massFlowCalculation.scalePower.max;
      }
      setFieldValue(e.target.name, e.target.value);
      setMassFlowCalculationS(() => {
        return {
          ...massFlowCalculationS,
          power: Number(e.target.value * 1000),
        };
      });
      setPowerDrugBtnState(() => {
        return {
          isDragging: false,
          x: powerDrugBtnState.x,
          y: value2pos(
            stat.userSetup.massFlowCalculation.scalePower,
            Number(e.target.value)
          ).y,
        };
      });
    }
  };

  return (
    <div>
      <Row>
        <Col item xs={12}>
          <div id="MassFlowNomographContainer">
            <MassFlowNomographUpdated
              setFieldValue={setFieldValue}
              deltaTDrugBtnState={deltaTDrugBtnState}
              massFlowCalculationS={massFlowCalculationS}
              setMassFlowCalculationS={setMassFlowCalculationS}
              setDeltaTDrugBtnState={setDeltaTDrugBtnState}
              powerDrugBtnState={powerDrugBtnState}
              setPowerDrugBtnState={setPowerDrugBtnState}
            />
          </div>
        </Col>
        <Col xs={12}>
          <div>
            <form>
              <Col xs={12} className="mt-2">
                <Form.Label>Delta Temp</Form.Label>
                <Form.Control
                  onChange={customHandleChange}
                  value={values.deltaTemp}
                  type="number"
                  placeholder="Delta Temp"
                  name="deltaTemp"
                />
              </Col>
              <Col item xs={12} className="mt-2">
                <Form.Label>Power [kW]</Form.Label>
                <Form.Control
                  onChange={customHandleChange}
                  value={values.power}
                  type="number"
                  placeholder="Power [kW]"
                  name="power"
                />
              </Col>
              <Col item xs={12} className="mt-2">
                <Form.Label>cb</Form.Label>
                <Form.Control
                  onChange={customHandleChange}
                  value={values.cb}
                  type="number"
                  placeholder="cb"
                  name="cb"
                />
              </Col>
              <Col item xs={12} className="mt-2">
                <Form.Label>rho</Form.Label>
                <Form.Control
                  onChange={customHandleChange}
                  value={values.rho}
                  type="number"
                  placeholder="rho"
                  name="rho"
                />
              </Col>
              <Button
                className="mt-2"
                variant="primary"
                onClick={(e) => {
                  e.preventDefault();
                  handleSubmit();
                }}
              >
                Result
              </Button>
            </form>
          </div>
        </Col>
      </Row>
    </div>
  );
};
