import React from "react";
import RoomsTable from "../ReportTables/RoomsTable";
import ProjectTable from "../ReportTables/ProjectTable";
import VerticalBar from "../ReportTables/charts/UiCharts/chart1";
import VerticalBar2 from "../ReportTables/charts/UiCharts/chart2";
import VerticalBar3 from "../ReportTables/charts/UiCharts/chart3";

const Report = () => {
  return (
    <div>
      <div
        style={{
          maxWidth: "100%",
        }}
        id="report"
      >
        <div
          style={{
            marginTop: "50px",
            backgroundColor: "white",
            borderRadius: "4px",
          }}
        >
          <ProjectTable />
        </div>
        <div
          elevation={0}
          style={{
            marginTop: "50px",
            backgroundColor: "white",
            borderRadius: "4px",
          }}
        >
          <RoomsTable />
        </div>
        <div
          elevation={0}
          style={{
            marginTop: "50px",
            backgroundColor: "white",
            borderRadius: "4px",
          }}
        >
          <div className="chart-container">
            <VerticalBar />
          </div>
        </div>
        <div
          elevation={0}
          style={{
            marginTop: "50px",
            backgroundColor: "white",
            borderRadius: "4px",
          }}
        >
          <div className="chart-container">
            <VerticalBar2 />
          </div>
        </div>
        <div
          elevation={0}
          style={{
            marginTop: "50px",
            backgroundColor: "white",
            borderRadius: "4px",
          }}
        >
          <div className="chart-container">
            <VerticalBar3 />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Report;
