import ModalWindow from "components/common/heatLossCalcCommon/ModalWindow/ModalWindow";
import { Row } from "react-bootstrap";
import { useFormik } from "formik";
import { useDispatch } from "react-redux";
import { edit_project_action } from "redux/store/project/actions";
import { getFormData } from "./functions/getFormData";
import { getFormUi } from "./functions/getFormUi";
import { getProjectData } from "./functions/getProjectData";

export const ProjectModalWindow = (props) => {
  const { project, show, onHide } = props;
  const dispatch = useDispatch();

  // initial values for formik form
  // gives from project state
  const initialFormValues = {
    name: project.name,
    outsideT: project.outsideT,
    groundT: project.groundT,
    dDays: project.dDays,
    buildingType: project.buildingType,
  };

  // use formik hook
  const { handleSubmit, handleChange, values } = useFormik({
    initialValues: initialFormValues,
    onSubmit: (values) => {
      const data = getProjectData(values);
      dispatch(edit_project_action(data));
      props.onHide();
    },
  });

  //get form data and create UI
  const formData = getFormData(values);
  const formUi = getFormUi(formData, handleChange);

  const modalBodyContent = <Row>{formUi}</Row>;

  return (
    <ModalWindow
      modalBodyContent={modalBodyContent}
      handleSubmit={handleSubmit}
      editStateTitle={"Edit project data"}
      normalStateTitle={"Project data"}
      isEdit={true}
      show={show}
      onHide={onHide}
    />
  );
};
