import NavigationPanel from "../../../common/heatLossCalcCommon/NavigationPanel/NavigationPanel";
import { withRouter } from "react-router-dom";

const RoomNavigationPanel = (props) => {
  const { navData, history } = props;

  const pageLevelLinks = [
    {
      link: "/main/mat-app",
      buttonLabel: "Go to project",
    },
  ];

  const neighbourPagesLinks = [
    {
      link: `/main/mat-app/room/${navData.prevPageId}`,
      buttonLabel: "Prev",
    },
    {
      link: `/main/mat-app/room/${navData.nextPageId}`,
      buttonLabel: "Next",
    },
  ];

  return (
    <NavigationPanel
      history={history}
      pageLevelLinks={pageLevelLinks}
      neighbourPagesLinks={neighbourPagesLinks}
    />
  );
};

export default withRouter(RoomNavigationPanel);
