import { NumberInput, TextInput } from "./ComponentData/inputs/inputs";
import { ConstructionTypeDropdown } from "./ComponentData/ModalInputComponents/DropdownInp";
import {
  ConstructionVariantSelect,
  RoomBuildingsTypeSelect,
} from "./ComponentData/ModalInputComponents/SelectInp";
import { Col } from "react-bootstrap";
import uValueList from "assets/uValueList/uValueList.json";

const SimpleRoomGroundFloor = (props) => {
  const { data, handlerChange, handlerTypeChange } = props;
  return (
    <>
      <Col sm={4} xs={6}>
        <RoomBuildingsTypeSelect
          value={data.exWallGroup}
          handlerChange={handlerTypeChange}
          isDisabled={false}
        />
      </Col>
      <Col sm={4} xs={6}>
        <TextInput
          placeholder="Name"
          value={data.name}
          name="name"
          handlerChange={handlerChange}
        />
      </Col>
      <Col sm={4} xs={6}>
        <NumberInput
          placeholder="Width [m]"
          value={data.width}
          name="width"
          handlerChange={handlerChange}
          step={0.1}
        />
      </Col>
      <Col sm={4} xs={6}>
        <NumberInput
          placeholder="Length [m]"
          value={data.length}
          name="length"
          handlerChange={handlerChange}
          step={0.1}
        />
      </Col>

      <Col sm={4} xs={6}>
        <NumberInput
          placeholder="Exposed perimeter [m]"
          value={data.expPerimeter}
          name="expPerimeter"
          handlerChange={handlerChange}
          step={0.1}
        />
      </Col>
      <Col sm={4} xs={6}>
        <NumberInput
          placeholder="Thermal conductivity [W/mK]"
          value={data.thermInsul}
          name="thermInsul"
          handlerChange={handlerChange}
          step={0.1}
        />
      </Col>
      <Col sm={4} xs={6}>
        <NumberInput
          placeholder="Thickness of insulation [mm]"
          value={data.thickInsul}
          name="thickInsul"
          handlerChange={handlerChange}
          step={0.1}
        />
      </Col>
    </>
  );
};

