const SimpleRoomRoof = (props) => {
  const {
    data,
    handlerChange,
    isExternal,
    handlerTypeChange,
    handlerDropdownChange,
    handlerVariantChange,
    choosedObj,
  } = props;
  return (
    <>
      <RoomBuildingsTypeSelect
        value={data.exWallGroup}
        handlerChange={handlerTypeChange}
        isDisabled={false}
      />
      <ConstructionTypeDropdown
        group_0={data.exWallGroup}
        items={uValueList.results}
        isExternal={isExternal}
        handlerChange={handlerDropdownChange}
        isHidden={choosedObj.exWallGroup}
      />
      <ConstructionVariantSelect
        handlerChange={handlerVariantChange}
        isDisabled={false}
        value={data.exWallGroup2}
        group_0={data.exWallGroup}
        group_1={data.exWallGroup1}
        items={uValueList.results}
        isHidden={choosedObj.exWallGroup1}
        isExternal={isExternal}
      />
      <TextInput
        placeholder="Name"
        value={data.name}
        name="name"
        handlerChange={handlerChange}
      />
      <NumberInput
        placeholder="Width [m]"
        value={data.width}
        name="width"
        handlerChange={handlerChange}
        step={0.1}
      />
      <NumberInput
        placeholder="Length [m]"
        value={data.length}
        name="length"
        handlerChange={handlerChange}
        step={0.1}
      />
      <NumberInput
        placeholder="U-value [W/mK]"
        value={data.uValue}
        name="uValue"
        handlerChange={handlerChange}
        step={0.1}
      />
    </>
  );
};

