export const ModalForm = (props) => {
  const {
    data,
    handlerChange,
    isExternal,
    roomType,
    group_0,
    handlerTypeChange,
    handlerDropdownChange,
    handlerVariantChange,
    choosedObj,
  } = props;

  if (group_0 === "ground floor") {
    if (roomType === "Simple room") {
      return (
        <SimpleRoomGroundFloor
          handlerTypeChange={handlerTypeChange}
          data={data}
          handlerChange={handlerChange}
        />
      );
    }
    if (roomType === "Complex shaped room") {
      return (
        <ComplexRoomGroundFloor
          handlerTypeChange={handlerTypeChange}
          data={data}
          handlerChange={handlerChange}
        />
      );
    }
  } else if (group_0 === "party floor") {
    if (roomType === "Simple room") {
      return (
        <SimpleRoomPartyFloor
          handlerTypeChange={handlerTypeChange}
          data={data}
          handlerChange={handlerChange}
        />
      );
    }
    if (roomType === "Complex shaped room") {
      return (
        <ComplexRoomPartyFloor
          handlerTypeChange={handlerTypeChange}
          data={data}
          handlerChange={handlerChange}
        />
      );
    }
  } else if (group_0 === "roof") {
    if (roomType === "Simple room") {
      return (
        <SimpleRoomRoof
          data={data}
          handlerChange={handlerChange}
          handlerTypeChange={handlerTypeChange}
          handlerDropdownChange={handlerDropdownChange}
          handlerVariantChange={handlerVariantChange}
          isExternal={isExternal}
          choosedObj={choosedObj}
        />
      );
    }
    if (roomType === "Complex shaped room") {
      return (
        <ComplexRoomRoof
          data={data}
          handlerChange={handlerChange}
          handlerTypeChange={handlerTypeChange}
          handlerDropdownChange={handlerDropdownChange}
          handlerVariantChange={handlerVariantChange}
          isExternal={isExternal}
          choosedObj={choosedObj}
        />
      );
    }
  } else {
    if (!isExternal) {
      return (
        <InternallWall
          data={data}
          handlerChange={handlerChange}
          handlerTypeChange={handlerTypeChange}
          handlerDropdownChange={handlerDropdownChange}
          handlerVariantChange={handlerVariantChange}
          isExternal={isExternal}
          choosedObj={choosedObj}
        />
      );
    } else {
      return (
        <ExternalWall
          data={data}
          handlerChange={handlerChange}
          handlerTypeChange={handlerTypeChange}
          handlerDropdownChange={handlerDropdownChange}
          handlerVariantChange={handlerVariantChange}
          isExternal={isExternal}
          choosedObj={choosedObj}
        />
      );
    }
  }
};
