const isNumber = function isNumber(value) {
  return typeof value === "number" && isFinite(value) && !isNaN(value);
};

export const validate_numbers = (number, numberName, objName) => {
  try {
    if (!isNumber(number)) {
      throw new SyntaxError("Error with number");
    }
  } catch (e) {
    // eslint-disable-next-line eqeqeq
    if (e.name == "SyntaxError") {
      console.warn(
        "Sorry, number wrong: infinity or NaN",
        "NUMBER: " + numberName + " OBJECT: " + objName
      );
    } else {
      throw e;
    }
  }
};
