import React from "react";
import { Bar } from "react-chartjs-2";
import { useSelector } from "react-redux";
import { calc_group } from "../../../calculation/calc_group";
import { toSiPrefix } from "math";

const options = {
  scales: {
    yAxes: [
      {
        ticks: {
          beginAtZero: true,
          fontSize: window.innerWidth < 600 ? 8 : 12,
        },
      },
    ],
    xAxes: [
      {
        barPercentage: 0.6,
        ticks: {
          fontSize: window.innerWidth < 600 ? 8 : 12,
        },
      },
    ],
  },
  plugins: {
    datalabels: {
      anchor: "center",
      align: "top",
      formatter: (value) => {
        return value + " W";
      },
      color: "white",
      backgroundColor: "#0D47A1",
      borderRadius: 5,
      font: {
        size: window.innerWidth < 600 ? 8 : 12,
      },
      labels: {
        title: {
          font: {},
        },
        value: {},
      },
    },
  },
  legend: {
    display: false,
  },
};

const VerticalBar2 = () => {
  const rooms = useSelector((state) => state.rooms);

  const getData = () => {
    let wallH = 0;
    let windowDoorH = 0;
    let roofH = 0;

    rooms.forEach((room) => {
      console.log(room);
      room.items.forEach((wall) => {
        console.log(wall);
        let wallItemsH = calc_group(wall.items, "heatLoss");
        console.log(wallItemsH);
        wallH += wall.heatLoss - wallItemsH;

        wall.items.forEach((item) => {
          if (item.exWallGroup === "roof") {
            roofH += item.heatLoss;
          }
          if (item.exWallGroup === "windows and doors") {
            windowDoorH += item.heatLoss;
          }
        });

        // windowDoorH += wallItemsH;
        console.log(windowDoorH);
        console.log(wallH);
      });
    });

    console.log(windowDoorH);
    console.log(wallH);
    return {
      wallHeatLoss: Number(toSiPrefix(wallH).value).toFixed(2),
      windowDoorHeatLoss: Number(toSiPrefix(windowDoorH).value).toFixed(2),
      roofHeatLoss: Number(toSiPrefix(roofH).value).toFixed(2),
    };
  };

  const { wallHeatLoss, windowDoorHeatLoss, roofHeatLoss } = getData();

  const data = {
    // labels: ["a", "b", "c", "d"],
    labels: ["Walls", "Windows/doors", "Floors", "Roof"],
    datasets: [
      {
        data: [wallHeatLoss, windowDoorHeatLoss, 0, roofHeatLoss],
        backgroundColor: "rgba(54, 162, 235, 0.5)",
        borderColor: "rgb(54, 162, 235)",
        borderWidth: 1.5,
      },
    ],
  };

  return (
    <>
      <div className="header">
        <h3
          className="title"
          style={{ textAlign: "center", padding: "20px", fontSize: "1.4rem" }}
        >
          Heat loss (building elements)
        </h3>
        {/* <ol type="a">
          <li>Walls</li>
          <li>Windows/doors</li>
          <li>Floors</li>
          <li>Roof</li>
        </ol> */}
      </div>
      <Bar data={data} options={options} />
    </>
  );
};

export default VerticalBar2;
