/* eslint-disable react-hooks/exhaustive-deps */
import ItemTable from "../../../common/heatLossCalcCommon/ItemTable/ItemTable";
import { useDispatch } from "react-redux";
import { getTableData } from "./functions/getTableData";
import { withRouter } from "react-router-dom";
import { useEffect } from "react";
import {
  calculate_item_data,
  calculate_room_data,
  calculate_wallItem_data,
  delete_item_action,
} from "../../../../redux/store/rooms/actions";
const RoomBuildingItemTable = (props) => {
  const {
    editRoomBuilding,
    deltaT,
    itemIdx,
    roomIdx,
    room,
    item,
    outsideT,
    rooms,
    projectBuildingType,
  } = props;

  const dispatch = useDispatch();

  const goToRoomBuildingInfoTable = () => {
    props.history.push(`/main/mat-app/room/${room.id}/wall/${item.id}`);
  };

  const deleteRoomBuildingItemTable = () => {

    if (window.confirm(`Do you want to delete ${item.name}?`)) {
      let deletedItem = document.getElementById(item.id);
      deletedItem.classList.add("flip-out-diag-2-br");
      setTimeout(() => {
        dispatch(delete_item_action(room.id, item.id));
        dispatch(
          calculate_room_data(roomIdx, {
            roomT: room.roomT,
            outsideT: outsideT,
          })
        );
      }, 600);
    }
  };

  const editRoomBuildingItemTable = () => {
    editRoomBuilding({ ...item, idx: itemIdx });
  };

  const tableData = getTableData(item, room.type);

  const calculateData = {
    deltaT: deltaT,
    buildingTypeValue: projectBuildingType,
  };

  const calculateRoomBuilding = () => {
    rooms[roomIdx].items[itemIdx].items.forEach((element, id) => {
      dispatch(calculate_wallItem_data(roomIdx, itemIdx, id, calculateData));
    });
    dispatch(calculate_item_data(roomIdx, itemIdx, calculateData));
    dispatch(
      calculate_room_data(props.roomIdx, {
        roomT: room.roomT,
        outsideT: outsideT,
      })
    );
  };

  useEffect(() => {
    calculateRoomBuilding();
  }, []);

  useEffect(() => {
    calculateRoomBuilding();
  }, [
    item.name,
    item.type,
    item.exWallGroup,
    item.thermInsul,
    item.thickInsul,
    item.expPerimeter,
    item["length"],
    item.height,
    item.uValue,
    item.floorArea,
    item.area,
    item.heatLoss,
    item.itemName,
  ]);

  return (
    <ItemTable
      tableId={item.id}
      tableName={item.name}
      goToInfoTable={goToRoomBuildingInfoTable}
      deleteTable={deleteRoomBuildingItemTable}
      hasInfoTable={true}
      editTable={editRoomBuildingItemTable}
      tableData={tableData}
    />
  );
};
export default withRouter(RoomBuildingItemTable);
