import React from "react";
import { Bar } from "react-chartjs-2";
import "chartjs-plugin-datalabels";
import { useSelector } from "react-redux";
import { toSiPrefix } from "math";

const data = (project) => {
  const labelsNormal = [
    "Transmission heat loss",
    "Ventilation heat loss",
    "Response capacity",
  ];
  const labelsSmall = ["Transmission", "Ventilation", "Response"];

  return {
    labels: window.innerWidth < 600 ? labelsSmall : labelsNormal,
    datasets: [
      {
        data: [
          Number(
            toSiPrefix(project.transmissionLoss, undefined, " ").value
          ).toFixed(2),
          Number(toSiPrefix(project.venHeatLoss, undefined, " ").value).toFixed(
            2
          ),
          0,
        ],
        backgroundColor: "rgba(54, 162, 235, 0.5)",
        borderColor: "rgb(54, 162, 235)",
        borderWidth: 1.5,
      },
    ],
  };
};

const options = {
  scales: {
    yAxes: [
      {
        ticks: {
          beginAtZero: true,
          fontSize: window.innerWidth < 600 ? 8 : 12,
        },
      },
    ],
    xAxes: [
      {
        barPercentage: 0.6,
        ticks: {
          fontSize: window.innerWidth < 600 ? 8 : 12,
        },
      },
    ],
  },
  plugins: {
    datalabels: {
      anchor: "center",
      align: "top",
      formatter: (value) => {
        return value + " W";
      },
      color: "white",
      backgroundColor: "#0D47A1",
      borderRadius: 5,
      font: {
        size: window.innerWidth < 600 ? 8 : 12,
      },
      labels: {
        title: {
          font: {},
        },
        value: {},
      },
    },
  },
  legend: {
    display: false,
  },
};

const VerticalBar = () => {
  const project = useSelector((state) => state.project);

  return (
    <>
      <div className="header">
        <h3
          className="title"
          style={{ textAlign: "center", padding: "20px", fontSize: "1.4rem" }}
        >
          Heat loss summary
        </h3>
        {/* <ol type="a">
          <li>Transmission heat loss</li>
          <li>Ventilation heat loss</li>
          <li>Response capacity</li>
        </ol> */}
      </div>
      <Bar data={data(project)} options={options} />
    </>
  );
};

export default VerticalBar;
