import { toSiPrefix } from "../../../../../math";

export const getTableData = (state, roomType) => {
  if (state.exWallGroup === "ground floor") {
    if (roomType === "Simple room") {
      return [
        {
          name: "Name",
          value: state.name,
          unit: "",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "Length",
          value: state["length"],
          unit: "m",
          prefData: toSiPrefix(state["length"], undefined, " "),
        },
        {
          name: "Width",
          value: state.width,
          unit: "m",
          prefData: toSiPrefix(state.width, undefined, " "),
        },
        {
          name: "Area",
          value: state.area,
          unit: "m",
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Exposed perimeter",
          value: state.expPerimeter,
          unit: "m",
          prefData: toSiPrefix(state.expPerimeter, undefined, " "),
        },
        {
          name: "Thermal conductivity of insulation",
          value: state.thermInsul,
          unit: "W/mK",
          prefData: toSiPrefix(state.thermInsul, undefined, " "),
        },
        {
          name: "Thickness of insulation",
          value: state.thickInsul,
          unit: "mm",
          prefData: toSiPrefix(state.thickInsul, undefined, " "),
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
      ];
    }
    if (roomType === "Complex shaped room") {
      return [
        {
          name: "Name",
          value: state.name,
          unit: "",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "Exposed perimeter",
          value: state.expPerimeter,
          unit: "m",
          prefData: toSiPrefix(state.expPerimeter, undefined, " "),
        },
        {
          name: "Area",
          value: state.area,
          unit: "m",
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Thermal conductivity of insulation",
          value: state.thermInsul,
          unit: "W/mK",
          prefData: toSiPrefix(state.thermInsul, undefined, " "),
        },
        {
          name: "Thickness of insulation",
          value: state.thickInsul,
          unit: "mm",
          prefData: toSiPrefix(state.thickInsul, undefined, " "),
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
      ];
    }
  } else if (state.exWallGroup === "roof") {
    if (roomType === "Simple room") {
      return [
        {
          name: "Name",
          value: state.name,
          unit: "",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "Length",
          value: state["length"],
          prefData: toSiPrefix(state["length"], undefined, " "),
          unit: "m",
        },
        {
          name: "Width",
          value: state.width,
          prefData: toSiPrefix(state.width, undefined, " "),
          unit: "m",
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Area",
          unit: "m",
          value: state.area,
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
        {
          name: "Description of layers",
          value: state.itemName,
          unit: "",
        },
      ];
    }
    if (roomType === "Complex shaped room") {
      return [
        {
          name: "Name",
          value: state.name,
          unit: "",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Area",
          unit: "m",
          value: state.area,
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
        {
          name: "Description of layers",
          value: state.itemName,
          unit: "",
        },
      ];
    }
  } else {
    if (state.external) {
      return [
        {
          name: "Name",
          value: state.name,
          col: "12",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "Length",
          value: state.length,
          prefData: toSiPrefix(state.length, undefined, " "),
          unit: "m",
        },
        {
          name: "Height",
          value: state.height,
          prefData: toSiPrefix(state.height, undefined, " "),
          unit: "m",
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Area",
          unit: "m",
          value: state.area,
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
        {
          name: "Description of layers",
          value: state.itemName,
          unit: "",
        },
      ];
    } else {
      return [
        {
          name: "Name",
          value: state.name,
          unit: "",
        },
        {
          name: "Type",
          value: state.exWallGroup,
          unit: "",
        },
        {
          name: "Length",
          value: state.length,
          prefData: toSiPrefix(state.length, undefined, " "),
          unit: "m",
        },
        {
          name: "Height",
          value: state.height,
          prefData: toSiPrefix(state.height, undefined, " "),
          unit: "m",
        },
        {
          name: "U-value",
          value: state.uValue,
          prefData: toSiPrefix(state.uValue, undefined, " "),
          unit: "W/mK",
        },
        {
          name: "Area",
          unit: "m",
          value: state.area,
          prefData: toSiPrefix(state.area, undefined, " "),
        },
        {
          name: "Heat loss",
          value: state.heatLoss,
          prefData: toSiPrefix(state.heatLoss, undefined, " "),
          unit: "W",
        },
        {
          name: "Description of layers",
          value: state.itemName,
          unit: "",
        },
        {
          name: "Temperature of neighbor room",
          value: state.tempNroom,
          prefData: toSiPrefix(state.tempNroom),
          unit: "",
        },
      ];
    }
  }
};
