import { toSiPrefix } from "../../../../../math";

export const getTableData = (state) => {
  if (state.type === "Simple room") {
    return [
      {
        name: "Name",
        value: state.name,
        unit: "",
      },
      {
        name: "Type",
        value: state.type,
        unit: "",
      },
      {
        name: "Width",
        value: state.width,
        prefData: toSiPrefix(state.width, undefined, " "),
        unit: "m",
      },
      {
        name: "Length",
        value: state["length"],
        prefData: toSiPrefix(state["length"], undefined, " "),
        unit: "m",
      },
      {
        name: "Height",
        value: state.height,
        prefData: toSiPrefix(state.height, undefined, " "),
        unit: "m",
      },
      {
        name: "Room temp",
        value: state.roomT,
        prefData: toSiPrefix(state.roomT, undefined, " "),
        unit: "",
      },
      {
        name: "Room volume",
        value: state.roomVolume,
        prefData: toSiPrefix(state.roomVolume, undefined, " "),
        unit: "m",
      },
      {
        name: "Room floor area",
        value: state.area,
        prefData: toSiPrefix(state.area, undefined, " "),
        unit: "m",
      },
      {
        name: "Exchange-rate",
        value: state.airExRate,
        prefData: toSiPrefix(state.airExRate, undefined, " "),
        unit: "AC/h",
      },
      {
        name: "Annual heating consumption",
        value: state.yearlyHeat,
        prefData: toSiPrefix(state.yearlyHeat, 0, "k"),
        unit: "Wh",
      },
      {
        name: "Ventilation-loss",
        value: state.venHeatLoss,
        prefData: toSiPrefix(state.venHeatLoss, undefined, " "),
        unit: "W",
      },
      {
        name: "Transmission-loss",
        value: state.transmissionLoss,
        prefData: toSiPrefix(state.transmissionLoss, undefined, " "),
        unit: "W",
      },
      {
        name: "Specific heat loss",
        value: state.specHeatLoss,
        prefData: toSiPrefix(state.specHeatLoss, undefined, " "),
        unit: "W/m",
      },
      {
        name: "Total-heat-loss",
        value: state.totalHeatLoss,
        prefData: toSiPrefix(state.totalHeatLoss, undefined, " "),
        unit: "W",
      },
    ];
  }
  if (state.type === "Complex shaped room") {
    return [
      {
        name: "Name",
        value: state.name,
        unit: "",
      },
      {
        name: "Type",
        value: state.type,
        unit: "",
      },
      {
        name: "Area",
        value: state.area,
        prefData: toSiPrefix(state.area, undefined, " "),
        unit: "m",
      },
      {
        name: "Height",
        value: state.height,
        prefData: toSiPrefix(state.height, undefined, " "),
        unit: "m",
      },
      {
        name: "Room temp",
        value: state.roomT,
        prefData: toSiPrefix(state.roomT, undefined, " "),
        unit: "",
      },
      {
        name: "Room volume",
        value: state.roomVolume,
        prefData: toSiPrefix(state.roomVolume, undefined, " "),
        unit: "m",
      },
      {
        name: "Room floor area",
        value: state.area,
        prefData: toSiPrefix(state.area, undefined, " "),
        unit: "m",
      },
      {
        name: "Exchange-rate",
        value: state.airExRate,
        prefData: toSiPrefix(state.airExRate, undefined, " "),
        unit: "AC/h",
      },
      {
        name: "Annual heating consumption",
        value: state.yearlyHeat,
        prefData: toSiPrefix(state.yearlyHeat, 0, "k"),
        unit: "Wh",
      },
      {
        name: "Ventilation-loss",
        value: state.venHeatLoss,
        prefData: toSiPrefix(state.venHeatLoss, undefined, " "),
        unit: "W",
      },
      {
        name: "Transmission-loss",
        value: state.transmissionLoss,
        prefData: toSiPrefix(state.transmissionLoss, undefined, " "),
        unit: "W",
      },
      {
        name: "Specific heat loss",
        value: state.specHeatLoss,
        prefData: toSiPrefix(state.specHeatLoss, undefined, " "),
        unit: "W/m",
      },
      {
        name: "Total-heat-loss",
        value: state.totalHeatLoss,
        prefData: toSiPrefix(state.totalHeatLoss, undefined, " "),
        unit: "W",
      },
    ];
  }
};
