import React from "react";
import { Col } from "react-bootstrap";
import RoomBuildingOpeningItemTable from "../RoomBuildingOpeningItemTable/RoomBuildingOpeningItemTable";

const RoomBuldingOpeningTables = (props) => {
  const items = props.items.map((wallItem, id) => {
    return (
      <Col xl={4} xs={12} lg={6} md={12} sm={12} key={id} className="mt-5">
        <RoomBuildingOpeningItemTable
          buildingTypeValue={props.buildingTypeValue}
          deltaT={props.deltaT}
          editRoomBuildingOpening={props.editRoomBuildingOpening}
          curRoomIdx={props.curRoomIdx}
          curRoomBuildingIdx={props.curRoomBuildingIdx}
          openingIdx={id}
          openingItem={wallItem}
        />
      </Col>
    );
  });
  return items;
};

export default RoomBuldingOpeningTables;
