import React from "react";
import { Circle } from "react-konva";
import { pos2value, value2pos } from "../../coreFunctions";
import { stat } from "../../settings";

const SetNomorgaphValue = (valueName, newValue, prevState, setState) => {
  setState(() => {
    const state = prevState;
    state[valueName] = newValue;
    return {
      ...state,
    };
  });
};

class NomographDrugBtn extends React.Component {
  constructor(props) {
    super(props);
    this.prefix = props.prefix;
    this.stat = stat;
    this.value2pos = value2pos;
    this.x = props.btnState.x;
    this.y = props.btnState.y;
    this.setBtnState = props.setBtnState;
    this.skaleProps = props.skaleProps;
    this.minValue = props.minValue;
    this.maxValue = props.maxValue;
    this.valueName = props.valueName;
  }

  render() {
    return (
      <Circle
        id={"100"}
        radius={24}
        stroke={
          this.props.btnState.isDragging ? "rgba(62, 127, 249, 1)" : "black"
        }
        strokeWidth={2}
        x={this.props.btnState.x}
        y={this.props.btnState.y}
        draggable
        fill={"rgba(62, 127, 249, 0.38)"}
        onDragStart={() => {
          this.props.setBtnState({
            ...this.props.btnState,
            isDragging: true,
          });
        }}
        onDragEnd={(e) => {
          this.props.setBtnState(() => {
            return {
              isDragging: false,
              x: e.target.x(),
              y: e.target.y(),
            };
          });
        }}
        dragBoundFunc={(pos) => {
          const currentValue = pos2value(this.skaleProps, pos.y);
          const startPos = value2pos(this.skaleProps, this.minValue);
          const endPos = value2pos(this.skaleProps, this.maxValue);
          const yPos =
            currentValue < this.minValue
              ? startPos
              : currentValue > this.maxValue
              ? endPos
              : pos.y;
          const newDataTemp =
            currentValue < this.minValue
              ? this.minValue
              : currentValue > this.maxValue
              ? this.maxValue
              : currentValue;

          SetNomorgaphValue(
            this.valueName,
            newDataTemp * this.prefix,
            this.props.massFlowCalculationS,
            this.props.setMassFlowCalculationS
          );
          this.props.setFieldValue(this.valueName, newDataTemp.toFixed(2));
          return {
            x: this.props.btnState.x,
            y: yPos,
          };
        }}
      />
    );
  }
}

export const NomographDeltaTDrugBtn = (props) => {
  return (
    <NomographDrugBtn
      valueName={"deltaTemp"}
      minValue={stat.userSetup.massFlowCalculation.scaleTemperature.min}
      maxValue={stat.userSetup.massFlowCalculation.scaleTemperature.max}
      prefix={1}
      setFieldValue={props.setFieldValue}
      skaleProps={stat.userSetup.massFlowCalculation.scaleTemperature}
      setMassFlowCalculationS={props.setMassFlowCalculationS}
      massFlowCalculationS={props.massFlowCalculationS}
      btnState={props.deltaTDrugBtnState}
      setBtnState={props.setDeltaTDrugBtnState}
    />
  );
};

export const NomographPowerBtn = (props) => {
  return (
    <NomographDrugBtn
      valueName={"power"}
      minValue={stat.userSetup.massFlowCalculation.scalePower.min}
      maxValue={stat.userSetup.massFlowCalculation.scalePower.max}
      prefix={1000}
      setFieldValue={props.setFieldValue}
      skaleProps={stat.userSetup.massFlowCalculation.scalePower}
      setMassFlowCalculationS={props.setMassFlowCalculationS}
      massFlowCalculationS={props.massFlowCalculationS}
      btnState={props.powerDrugBtnState}
      setBtnState={props.setPowerDrugBtnState}
    />
  );
};
