import { Form } from "react-bootstrap";

const SelectInput = (props) => {
  const {
    placeholder,
    name,
    value,
    handlerChange,
    isDisabled,
    options,
    isHidden,
  } = props;

  return (
    <Form.Group
      className={isHidden ? "" : "hidden"}
      style={{ transition: "opacity 0.3s linear" }}
    >
      <Form.Label>{placeholder}</Form.Label>
      <Form.Control
        disabled={isDisabled}
        size="sm"
        as="select"
        name={name}
        value={value}
        onChange={handlerChange}
      >
        {options}
      </Form.Control>
    </Form.Group>
  );
};

export const RoomBuildingsTypeSelect = (props) => {
  const { value, handlerChange, isDisabled } = props;

  const options = (
    <>
      <option selected="true" disabled="disabled">
        Choose type
      </option>
      <option>external wall</option>
      <option>external cavity wall</option>
      <option>internal wall</option>
      <option>party wall</option>
      <option>roof</option>
      <option>ground floor</option>
      <option>party floor</option>
    </>
  );

  return (
    <SelectInput
      handlerChange={handlerChange}
      isDisabled={isDisabled}
      isHidden={true}
      placeholder="Type"
      value={value}
      name="exWallGroup"
      options={options}
    />
  );
};

export const ConstructionVariantSelect = (props) => {
  const {
    value,
    handlerChange,
    isDisabled,
    group_0,
    group_1,
    items,
    isExternal,
    isHidden,
  } = props;

  const get_variant_building_options = () => {
    const options_group_2 = items.filter((el) => {
      return (
        el.group_0 === group_0 &&
        el.group_1 === group_1 &&
        el.external === isExternal
      );
    });

    const options_group_2_clear = options_group_2.map((el) => {
      return {
        name: el.group_2,
      };
    });

    return options_group_2_clear;
  };

  const options = get_variant_building_options().map((option, id) => {
    return <option key={id}>{option.name}</option>;
  });

  return (
    <SelectInput
      handlerChange={handlerChange}
      isDisabled={isDisabled}
      placeholder="Variant"
      value={value}
      name="exWallGroup2"
      options={options}
      isHidden={isHidden}
    />
  );
};
