import { Form, Dropdown } from "react-bootstrap";

const DropdownInp = (props) => {
  const { dropdownItems, label, btnLabel, isHidden } = props;

  return (
    <Form.Group
      className={isHidden ? "" : "hidden"}
      style={{ transition: "opacity 0.3s linear" }}
    >
      <Form.Label>{label}</Form.Label>
      <Dropdown style={{ width: "100%" }}>
        <Dropdown.Toggle
          style={{ width: "100%" }}
          id="dropdown-basic"
          className="items-type-dropdown-btn"
        >
          <div className="items-type-dropdown-btn__content">{btnLabel}</div>
        </Dropdown.Toggle>
        <Dropdown.Menu
          className="modal-window__dropdown-menu"
          style={{ width: "100%" }}
        >
          {dropdownItems}
        </Dropdown.Menu>
      </Dropdown>
    </Form.Group>
  );
};

export const ConstructionTypeDropdown = (props) => {
  const { group_0, items, isExternal, handlerChange, isHidden } = props;

  const get_construction_type_options = () => {
    // files that sort with group 0
    const items_group_0 = items.filter((el) => {
      return el.group_0 === group_0 && el.external === isExternal;
    });

    // unique files that sort with group 1, need for generating options
    const options_group_1 = Object.values(
      items_group_0.reduce((c, e) => {
        if (!c[e.group_1]) c[e.group_1] = e;
        return c;
      }, {})
    );

    const options_group_1_clear = options_group_1.map((el) => {
      return {
        option: el.group_1,
        img: el.file,
        name: el.name,
      };
    });

    return options_group_1_clear;
  };

  const dropdownItems = get_construction_type_options().map((el, id) => {
    return (
      <Dropdown.Item
        className="wall-dropdown-item"
        key={id}
        value={el.option}
        img={el.img}
        name="exWallGroup1"
        onClick={handlerChange}
      >
        <img
          alt="wall item img"
          src={process.env.PUBLIC_URL + `/images/WallType/${el.img}`}
          style={{ width: "250px", height: "200px" }}
          className="mr-2"
        />
        {el.option}
      </Dropdown.Item>
    );
  });

  return (
    <DropdownInp
      isHidden={isHidden}
      dropdownItems={dropdownItems}
      label="Construction type"
      btnLabel="Select construction type"
    />
  );
};
