import { toSiPrefix } from "../../../../../math";

export const getTableData = (state) => {
  return [
    {
      name: "Temp outside",
      value: state.outsideT,
      unit: "",
    },
    {
      name: "Ground temp",
      value: state.groundT,
      unit: "",
    },
    {
      name: "Degree-days",
      value: state.dDays,
      unit: "",
    },
    {
      name: "Total floor area",
      value: state.totalFloorArea,
      prefData: toSiPrefix(state.totalFloorArea, undefined, " "),
      unit: "m",
    },
    {
      name: "Annual heating consumption",
      value: state.yearlyHeat,
      prefData: toSiPrefix(state.yearlyHeat, 0, "k"),
      unit: "Wh",
    },
    {
      name: "Height correction",
      value: state.ceilingHeatLoss,
      prefData: toSiPrefix(state.ceilingHeatLoss, undefined, " "),
      unit: "W",
    },
    {
      name: "Ventilation-loss",
      value: state.venHeatLoss,
      prefData: toSiPrefix(state.venHeatLoss, undefined, " "),
      unit: "W",
    },
    {
      name: "Transmission-loss",
      value: state.transmissionLoss,
      prefData: toSiPrefix(state.transmissionLoss, undefined, " "),
      unit: "W",
    },
    {
      name: "Total-heat-loss",
      value: state.totalHeatLoss,
      prefData: toSiPrefix(state.totalHeatLoss, undefined, " "),
      unit: "W",
    },
    {
      name: "Specific heat loss",
      value: state.specHeatLoss,
      prefData: toSiPrefix(state.specHeatLoss, undefined, " "),
      unit: "W/m",
    },
    {
      name: "Thermal bridges",
      value: state.buildingType,
      prefData: toSiPrefix(state.buildingType, undefined, " "),
      unit: "W/mK",
    },
  ];
};
