/* eslint-disable react-hooks/exhaustive-deps */
import ModalWindow from "../../../common/heatLossCalcCommon/ModalWindow/ModalWindow";
import { useDispatch } from "react-redux";
import { useFormik } from "formik";
import { getInitialFormValues } from "./functions/getInitialFormValues";
import RoomModalForm from "./RoomModalForm";
import { getRoomData } from "./functions/getRoomData";
import { add_room_action, edit_room_action } from "redux/store/rooms/actions";
import { useEffect, useState } from "react";

const RoomModalWindow = (props) => {
  const { isEdit, show, onHide, editedRoom } = props;
  const dispatch = useDispatch();
  const editStateTitle = `Edit room ${editedRoom.name}`;
  const normalStateTitle = "Add a new room";

  // state for Value - heating power
  const [heatPower, setheatPower] = useState(0);

  const { handleSubmit, handleChange, values, resetForm, setValues } =
    useFormik({
      initialValues: getInitialFormValues(),

      onSubmit: (values) => {
        const roomData = getRoomData(values, heatPower);
        console.log(roomData);
        isEdit
          ? dispatch(edit_room_action(editedRoom.id, roomData))
          : dispatch(add_room_action(roomData));
        onHide();
      },
    });

  useEffect(() => {
    if (!show) {
      setTimeout(() => {
        resetForm();
      }, 300);
    }
  }, [show, resetForm]);

  useEffect(() => {
    if (isEdit) {
      let choosedRoomData = {};
      if (editedRoom.type === "Simple room") {
        choosedRoomData = {
          name: editedRoom.name,
          type: editedRoom.type,
          roomT: editedRoom.roomT,
          airExRate: editedRoom.airExRate,
          width: editedRoom.width,
          area: 0,
          height: editedRoom.height,
          length: editedRoom.length,
          disusePeriod: editedRoom.disusePeriod,
          bAir: editedRoom.bAir,
          bMass: editedRoom.bMass,
          reheatTime: editedRoom.reheatTime,
        };
      } else {
        choosedRoomData = {
          name: editedRoom.name,
          type: editedRoom.type,
          roomT: editedRoom.roomT,
          airExRate: editedRoom.airExRate,
          width: 0,
          area: editedRoom.area,
          height: editedRoom.height,
          length: 0,
          disusePeriod: editedRoom.disusePeriod,
          bAir: editedRoom.bAir,
          bMass: editedRoom.bMass,
          reheatTime: editedRoom.reheatTime,
        };
      }
      setValues(choosedRoomData);
    }
  }, [isEdit, setValues]);

  const modalBodyContent = (
    <RoomModalForm
      values={values}
      handleChange={handleChange}
      heatPower={heatPower}
      setheatPower={setheatPower}
    />
  );

  return (
    <ModalWindow
      modalBodyContent={modalBodyContent}
      editStateTitle={editStateTitle}
      normalStateTitle={normalStateTitle}
      handleSubmit={handleSubmit}
      isEdit={isEdit}
      show={show}
      onHide={onHide}
    />
  );
};

export default RoomModalWindow;
