import Footer from "components/app-components/AppFooter/AppFooter";
import Header from "components/app-components/AppHeader/AppHeader";
import Sidebar from "components/app-components/AppSidebar/AppSidebar";
import routes from "routes.js";
import { Route, Switch } from "react-router-dom";

const MainLayout = () => {
  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/main") {
        return (
          <Route
            path={prop.layout + prop.path}
            render={(props) => <prop.component {...props} />}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  return (
    <>
      <Header sidebarBtn={<Sidebar routes={routes} name={"Open sidebar"} />} />
      <div className="content">
        <Switch>{getRoutes(routes)}</Switch>
      </div>
      {/* <Footer /> */}
    </>
  );
};
export default MainLayout;
