import React, { useEffect } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { calculate_project_data } from "./redux/store/project/actions";
import ClearStorageAlertDialog from "./components/app-components/ClearStorageAlertDialog/ClearStorageAlertDialog";
import MainLayout from "layouts/Main";
import "bootstrap/dist/css/bootstrap.min.css";

const App = () => {
  const rooms = useSelector((state) => state.rooms);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(calculate_project_data(rooms));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    dispatch(calculate_project_data(rooms));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [rooms]);

  return (
    <>
      <Switch>
        <Route path="/main" render={(props) => <MainLayout {...props} />} />
        <Redirect from="/" to="/main/start" />
      </Switch>
      <ClearStorageAlertDialog />
    </>
  );
};

export default App;
