import React, { useState } from "react";
import editIcon from "assets/images/svg/edit.svg";
import { Card, Table, Accordion, useAccordionButton } from "react-bootstrap";
import { getUi } from "./functions/getUi";
import { NavLink } from "react-router-dom";

const ItemTable = (props) => {
  const {
    tableId,
    tableName,
    goToInfoTable,
    editTable,
    deleteTable,
    tableData,
    hasInfoTable,
  } = props;

  const tableUi = getUi(tableData);

  const [acTogleTxt, acSetTogleTxt] = useState("Show data");

  function CustomToggle({ children, eventKey }) {
    const decoratedOnClick = useAccordionButton(eventKey, (e) => {
      e.target.innerText === "Show data"
        ? acSetTogleTxt("Hide data")
        : acSetTogleTxt("Show data");
    });

    return (
      <button className="ml-3 button" eventKey="0" onClick={decoratedOnClick}>
        {acTogleTxt}
      </button>
    );
  }

  const goToBtn = hasInfoTable ? (
    <button onClick={goToInfoTable} className="button mr-3">
      add/edit elements
    </button>
  ) : (
    ""
  );

  const tableButtons = hasInfoTable ? (
    <div className="d-flex justify-content-between align-items-center flex-wrap">
      <div className="card_item__event-btns">
        <NavLink to="/main/mat-app/room/799c2a6a-2afe-4c86-b9e0-4b3076f2d241">
frerr
        </NavLink>
        {goToBtn}
        <button className="button button_edit" onClick={editTable}>
          <img
            className="button__img button_edit__img"
            src={editIcon}
            alt="Edit"
          />
        </button>
        <CustomToggle eventKey="0" />
      </div>
      <button
        className="button button_delete card_item__button_delete"
        onClick={deleteTable}
      >
        Delete
      </button>
    </div>
  ) : (
    <div className="d-flex justify-content-between align-items-center flex-wrap">
      <div className="card_item__event-btns card_item__event-btns_ended">
        <button className="button button_edit" onClick={editTable}>
          <img
            className="button__img button_edit__img"
            src={editIcon}
            alt="Edit"
          />
        </button>
        <CustomToggle eventKey="0" />
      </div>
      <button
        className="button button_delete card_item__button_delete card_item__button_delete_ended"
        onClick={deleteTable}
      >
        Delete
      </button>
    </div>
  );

  return (
    <Accordion defaultActiveKey="1">
      <Card className="card card_item" id={tableId}>
        <Card.Header className="card__header card_item__header">
          <div className="card_item__name text-center mb-2">{tableName}</div>
          {tableButtons}
        </Card.Header>
        <Accordion.Collapse eventKey="0">
          <Card.Body className="card__body card_item__body">
            <Table striped bordered hover>
              <tbody>{tableUi}</tbody>
            </Table>
          </Card.Body>
        </Accordion.Collapse>
      </Card>
    </Accordion>
  );
};

export default ItemTable;
