import React from "react";
import AppCard from "../components/app-components/AppCard/AppCard";
import { Row, Col, Container } from "react-bootstrap";
import AppHeader from "components/app-components/AppHeader/AppHeader";

const StartPage = () => {
  const matAppDesc = `This is an application for calculating the heat loss of a building or individual rooms`;
  const wrongDesc = `This application is under development or has not been added`;
  const nomogramDesc = `A collection of Nomographs to simplify daily tasks`;

  const content = (
    <Row style={{ justifyContent: "space-between" }}>
      <Col item xs={12} sm={6} md={4} lg={3}>
        <AppCard
          desc={matAppDesc}
          img={"valuation-calculator-app-logo.png"}
          title={"Heat loss application"}
          link={"/main/mat-app"}
        />
      </Col>
      <Col item xs={12} sm={6} md={4} lg={3}>
        <AppCard
          desc={nomogramDesc}
          img={"Nomogram.png"}
          title={"Nomograph Collection"}
          link={"/main/nomograph"}
        />
      </Col>
      <Col item xs={12} sm={6} md={4} lg={3}>
        <AppCard
          desc={wrongDesc}
          img={"wrong.png"}
          title={"Coming soon"}
          link={"/main/start"}
        />
      </Col>
    </Row>
  );

  return (
    <>
      <Container fluid>{content}</Container>
    </>
  );
};

export default StartPage;
