import { Nav } from "react-bootstrap";

const NavigationPanel = (props) => {
  const { pageLevelLinks, neighbourPagesLinks } = props;

  const NavBtn = (props) => {
    const { goToLink, buttonLabel } = props;
    return (
      <button className="button" onClick={goToLink}>
        {buttonLabel}
      </button>
    );
  };

  const createLinksUi = (linkArray) => {
    return linkArray.map((linkObj, id) => {
      const goToLink = () => {
        props.history.push(linkObj.link);
      };
      return (
        <Nav.Item key={id} className={id % 2 !== 0 ? " " : "mr-2"}>
          <NavBtn goToLink={goToLink} buttonLabel={linkObj.buttonLabel} />
        </Nav.Item>
      );
    });
  };

  const pageLevelLinksUi = createLinksUi(pageLevelLinks);
  const neighbourPagesLinksUi = createLinksUi(neighbourPagesLinks);

  return (
    <Nav className="nav_room">
      <div className="d-flex">{pageLevelLinksUi}</div>
      <div className="d-flex">{neighbourPagesLinksUi}</div>
    </Nav>
  );
};

export default NavigationPanel;
